:root {
    --screen-ratio : calc(9 / 16);
    --screen-height : calc(min(100vw / var(--screen-ratio), 100vh));
    --screen-width : calc(min(100vw, 100vh * var(--screen-ratio)));
}
::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    background: transparent; /* make scrollbar transparent */
  }

.screen {    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    background-color: beige;
    width: 100vw;  
    height: 100vh;  
}

.game-scrollbox {
    display: block;
    background-color: palevioletred;
    width: var(--screen-width);    
    height: var(--screen-height);
    overflow: scroll;
}


.game {
    display: flex;
    flex-direction: column;    
    justify-content : space-between;
    background-color: palevioletred;
    width: var(--screen-width);    
    height: calc(var(--screen-height) + 50px);
}

.header-area {
    background-color: powderblue;
    height: calc(var(--screen-width) / 4);
}
.game-area {
}
.game-area-open {
    background-color: darkgrey;
    height: calc(var(--screen-height) - var(--screen-width) / 2);
    transition: height 0.3s ease-in-out;
}
.game-area-closed {
    background-color: darkgrey;
    height: 0px;
    transition: height 0.3s ease-in-out;
}

.weapon-area {    
    display: flex;
    flex-direction: row;
    justify-content : space-around;
    align-items : center;
    background-color: powderblue;
    height: calc(var(--screen-width) / 4);
}
.weapon-button {
    background-color: thistle;
    border-radius: 15%;
    width: calc(var(--screen-width) / 4 * 0.8);
    height: calc(var(--screen-width) / 4 * 0.8);
}
.armory-area {
    background-color: darkslateblue;
    flex-grow: 1;
}
